<template>
  <h1 class="my-5">Stock</h1>
  <div class="card mt-4">
    <table class="table m-0">
      <thead>
        <tr>
          <th scope="col">Producto</th>
          <th scope="col">Cantidad</th>
          <th scope="col">Precio</th>
          <th scope="col">Accion</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="{ id, cantidad, nombre, precio } in items" :key="id">
          <td>{{ nombre }}</td>
          <td>{{ cantidad }}</td>
          <td>{{ precio }}</td>
          <td>
            <router-link :to="`/stock-edit/${id}`">
              <button class="btn btn-primary btn-sm me-2">Editar</button>
            </router-link>
            <button class="btn btn-danger btn-sm" @click="deleteItem(id)">
              Borrar
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>


<script>

import { useLoadItems, deleteItem, updateItem  } from "@/firebase";


export default {
  setup() {
    const items = useLoadItems()
    return { items, deleteItem, updateItem }
  },
};
</script>